.App {
  text-align: center;
}

.reservation-info {
  background-color: #2f66d4;
  color: white;
  /* height: 40vmin; */
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.header {
  background-color: #2f66d4;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.submit_button,
a.submit_button {
  color: #fff;
  background-color: #eb6100;
}
.submit_button:hover,
a.submit_button:hover {
  color: #fff;
  background: #f56500;
}
a.submit_button {
  border-radius: 100vh;
}
